import './index.scss'
const button = document.querySelector('.blue-btn')
const authorsInput = document.querySelector('input[name=authors]')
const form = document.querySelector('form')

console.log(authorsInput)
button.addEventListener('click', (e) => {
  e.preventDefault()
  const authorsValue = authorsInput.value.split(',')
  console.log(authorsValue)
  authorsInput.value = authorsValue
  form.submit()
})
